<template lang="pug">
    div
        ul.breadcrumb
            li.breadcrumbItem
                a.active(href="#") 首頁
            li.breadcrumbItem
                a(href="#") {{commits[0].Name}}
        .contentCard
            .contentImg
                img(:src="commits[0].Picture1", alt="")
            .cardContent
                h4.cardTitle {{commits[0].Name}}
                .listCategory
                    strong {{commits[0].Name}}
                    span {{commits[0].Zone}}
                ul.listInfo
                    li
                        i.fas.fa-map-marker-alt
                        | {{commits[0].Add}}
                    li
                        i.far.fa-calendar-alt
                        | {{commits[0].Opentime}}
                p 
                    | {{commits[0].Description}}
</template>

<script>
export default {
  name: "content",
  data() {
    return {
      commits: []
    };
  },
  created() {
    let vm = this;
    const id = this.$route.params.id;
    vm.$http
      .get(
        "https://data.kcg.gov.tw/api/action/datastore_search?resource_id=92290ee5-6e61-456f-80c0-249eae2fcc97"
      )
      .then(response => {
        let data = response.data.result.records;
        vm.commits = data.filter(item => item.Id == id);
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



