<template lang="pug">
    .leftMenuCtl
        .formGroup
          .title 區域
          select(v-model="locationValue" @change="locationFun")
            option(selected value="all") 全部區域
            option(:value='item' v-for="item in location") {{item}}
        // .formGroup
        //   .title Date
        //   .formrow
        //     label(for='from') from
        //     input#from(type='date', value='2018-05-24')
        //   .formrow
        //     label(for='to') to
        //     input#to(type='date', value='2018-06-01')
        .formGroup
          .title 篩選條件
          .formrow
            input#free(type='checkbox' value="免費參觀" v-model="freeValue" @change="freeFun")
            label(for='free' style="width: 100px;") 免費參觀
          .formrow
            input#allDay(type='checkbox' value="全天候開放" v-model="allDayValue" @change="allDayFun")
            label(for='allDay' style="width: 100px;") 全天候開放
</template>

<script>
export default {
  props: ["dataArray"],
  name: "leftmenu",
  data() {
    return {
      dataCommits: [],
      location: [],
      freeValue: false,
      allDayValue: false,
      locationValue: "all"
    };
  },
  watch: {
    dataArray() {
      this.dataCommits = this.dataArray;
      this.filterZone();
    }
  },
  methods: {
    freeFun: function() {
      this.$emit("emitFree");
    },
    allDayFun: function() {
      this.$emit("emitAllDay");
    },
    locationFun: function() {
      this.$emit("emitLocation", this.locationValue);
    },
    filterZone: function() {
      let vm = this;
      let zoneTemp = [];
      vm.dataCommits.forEach(item => {
        zoneTemp.push(item.Zone);
      });
      vm.location = zoneTemp.filter(function(element, index, arr) {
        return arr.indexOf(element) === index;
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



