import Vue from 'vue'
import Router from 'vue-router'

import cardList from '@/components/cardlist'
import Content from '@/components/content'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'cardList',
      component: cardList,
      props: true,
    },
    {
      path: '/content/:id',
      name: 'content',
      component: Content,
      props: true
    },
  ]
})



