<template lang="pug">
  #app
    header
      .container
        .contentLeft
          a.logo(href="#") HaveFun
        .contentRight
          .searchBox
            i.fas.fa-search
            input.searchInput(type="text" placeholder="Explore your own activities" v-model="searchName")
    .content
      .container
        .contentLeft
          Left-menu(@emitLocation="locationCurrent" @emitFree="freeCurrent"  @emitAllDay="allDayCurrent" :dataArray="commits")
        .contentRight
          router-view(:commitsList="computeList")
</template>

<script>
import LeftMenu from "./components/leftmenu";
export default {
  name: "App",
  data() {
    return {
      commits: [],
      tt: [],
      location: [],
      filterActive: [],
      freeValue: false,
      allDayValue: false,
      locationActive: "all",
      searchName: ""
    };
  },
  components: {
    LeftMenu
  },
  created() {
    this.loadData();
  },
  computed: {
    computeList() {
      let vm = this;
      // let filterTemp = [];
      // console.log(vm.commits);
      // if (vm.freeValue) {
      //   filterTemp = vm.commits.filter(item => item.Ticketinfo == "免費參觀");
      // } else if (vm.allDayValue) {
      //   filterTemp = vm.commits.filter(item => item.Opentime == "全天候開放");
      // } else {
