      //   filterTemp = vm.commits;
      // }
      // vm.commits = filterTemp;
      // return filterTemp;

      // let filterTemp = vm.commits;
      // let [a, b] = vm.filterActive;
      // let tt = vm.commits.filter(item => item.Ticketinfo == "免費參觀");

      // vm.filterActive.forEach(item => {
      //   if (item == "免費參觀") {
      //     filterTemp = vm.commits.filter(item => item.Ticketinfo == "免費參觀");
      //   } else if (item == "全天候開放") {
      //     filterTemp = vm.commits.filter(item => item.Opentime == "全天候開放");
      //   } else {
      //     filterTemp = vm.commits;
      //   }
      // });

      //  else if (vm.allDayValue) {
      //     return vm.commits.filter(item => item.Opentime == "全天候開放");
      //   }

