      //     return vm.commits.filter(item => item.Ticketinfo == "免費參觀");
      //   } else {
      //     return vm.commits;
      //   }
      // });

      // console.log(vm.commits);
      // let filterTemp = [];
      // if (vm.freeValue) {
      //   vm.commits.forEach(item => {
      //     filterTemp.push(item);
      //   });
      // }
      // return filterTemp;

      let filterTemp = [];
      if (vm.locationActive == "all") {
        filterTemp = vm.commits;
      } else if (vm.locationActive != "") {
        filterTemp = vm.commits.filter(item => item.Zone == vm.locationActive);
      }
      return filterTemp;
    }
  },
  methods: {
    loadData: function() {
      let vm = this;
      vm.$http
        .get(
          "https://data.kcg.gov.tw/api/action/datastore_search?resource_id=92290ee5-6e61-456f-80c0-249eae2fcc97"
        )
        .then(response => {
          vm.commits = response.data.result.records;
          // console.log(vm.commits);
        });
    },
    locationCurrent: function(...value) {
      this.locationActive = value[0];
    },
    freeCurrent: function() {
      this.freeValue = !this.freeValue;
      console.log(this.freeValue);
    },
    allDayCurrent: function() {
      this.allDayValue = !this.allDayValue;
      console.log(this.allDayValue);
    }
  }
};
</script>
<style lang="scss">
@import "./assets/css/all.scss";
@import "font-awesome/css/font-awesome.css";
