<template lang="pug">
  div
    .keywordBox
      .titleNum
        | Showing
        span {{commitsList.length}}
        | results by…
      ul.listItem
    .cardList
      template(v-for="record in commitsList")
        router-link.cardItem(:to="`/content/${record.Id}`")
          .cardImg
            img(:src='record.Picture1', alt='')
          .cardContent
            h4.cardTitle {{record.Name}}
            p.cardText
              | {{record.Description}}}
            .listCategory
              strong {{record.Name}}
              span {{record.Zone}}
            ul.listInfo
              li
                i.fas.fa-map-marker-alt
                | {{record.Add}}
              li
                i.far.fa-calendar-alt
                | {{record.Opentime}}
    ul.paginationBox
            li
              a(href='#') «
            li
              a.active(href='#') 1
            li
              a(href='#') 2
            li
              a(href='#') 3
            li
              a(href='#') 4
            li
              a(href='#') 5
            li
              a(href='#') »
</template>

<script>
export default {
  props: ["commitsList"],
  name: "HelloWorld"
};
</script>

<style>
</style>



